import * as React from 'react'
import styled from '../styles/styled-components'
import { BackgroundImage } from './BackgroundImage'
import { Container } from './Container'

const Wrapper = styled.section`
  position: relative;
  width: 100%;
  padding-top: 51.66%;
`

const Contents = styled.div`
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
`

interface CardContainerProps {
  className?: string
  bgImage?: SharpSizesImage
}

export const CardContainer: React.SFC<CardContainerProps> = ({ bgImage, className, children }) => (
  <Container>
    <Wrapper>
      {bgImage && <BackgroundImage image={bgImage} />}
      <Contents className={className}>{children}</Contents>
    </Wrapper>
  </Container>
)



