import * as React from 'react'
import styled from '../styles/styled-components'
import { BackgroundImage } from './BackgroundImage'
import { CardContainer } from './CardContainer'

const TwoCardContainer = styled(CardContainer)`
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
`

const Pane = styled.div`
  position: relative;
  flex-basis: 50%;
  width: 50%;
  max-width: 50%;
  flex-grow: 1;
  flex-shrink: 1;
`

const PaneContents = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`

const LeftPane = styled(Pane)`
  padding-right: ${p => p.theme.dimensions.containerPadding}rem;
`

const RightPane = styled(Pane)`
  padding-left: ${p => p.theme.dimensions.containerPadding}rem;
`

interface WorkTwoCardsProps {
  leftImage: SharpSizesImage
  rightImage: SharpSizesImage
}

export const WorkTwoCards: React.SFC<WorkTwoCardsProps> = ({ leftImage, rightImage }) => (
  <TwoCardContainer>
    <LeftPane>
      <PaneContents>
        <BackgroundImage image={leftImage} />
      </PaneContents>
    </LeftPane>
    <RightPane>
      <PaneContents>
        <BackgroundImage image={rightImage} />
      </PaneContents>
    </RightPane>
  </TwoCardContainer>
)



