import * as React from 'react'
import styled from '../styles/styled-components'
import { CardContainer } from './CardContainer'

const IntroContainer = styled(CardContainer)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
`

const Contents = styled.div`
  max-width: 786px;

  p {
    font-size: 1.375rem;
  }
`

interface IntroProps {
  bgImage: SharpSizesImage
}

export const Intro: React.SFC<IntroProps> = ({ bgImage }) => (
  <IntroContainer bgImage={bgImage}>
    <Contents>
      <h1>
        IDEAS
        <br />
        TURNED PRODUCTS
      </h1>
      <p>Makeomatic is your cutting-edge web development and design partner specializing in creating high-load interactive applications.</p>
    </Contents>
  </IntroContainer>
)



