import * as React from 'react'
import GatsbyImage from 'gatsby-image'
import styled from '../styles/styled-components'

const BackgroundImageContainer = styled.div`
  position: absolute;
  top: -${p => p.theme.dimensions.containerPadding}rem;
  left: -${p => p.theme.dimensions.containerPadding}rem;
  bottom: -${p => p.theme.dimensions.containerPadding}rem;
  right: -${p => p.theme.dimensions.containerPadding}rem;
`

interface BackgroundImageProps {
  className?: string
  image: SharpSizesImage
}

export const BackgroundImage: React.SFC<BackgroundImageProps> = ({ image, className }) => (
  <BackgroundImageContainer className={className}>
    <GatsbyImage sizes={image.sizes} />
  </BackgroundImageContainer>
)



